import React from "react";
import { NavHashLink as Link } from "react-router-hash-link";
import { Container, Navbar, Nav, NavDropdown } from "react-bootstrap";
import { withState } from "./../Context";

const MainNavigation = withState(
  ({
    state: {
      translations: { t, i18n },
      name,
      apartments,
      scrollWithOffset
    }
  }) => {
    const lang = {
      en: `Language (${i18n.language})`,
      hr: `Jezik (${i18n.language})`,
      it: `Italiano (${i18n.language})`,
      de: `Deutsch (${i18n.language})`
    };

    return (
      <Navbar bg="dark" variant="dark" expand="lg" fixed="top">
        <Container>
          <Link
            className="navbar-brand"
            smooth
            to="/#"
            scroll={(el) => scrollWithOffset(el)}
          >
            {name && name}
          </Link>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse className="justify-content-end">
            <Nav>
              <Link
                smooth
                className="nav-link"
                activeClassName="active"
                to="/#"
                scroll={(el) => scrollWithOffset(el)}
              >
                {t("navbarHome")}
              </Link>
              <Link
                smooth
                className="nav-link"
                activeClassName="active"
                to="/rab-island#"
                scroll={(el) => scrollWithOffset(el)}
              >
                {t("navbarIslandRab")}
              </Link>

              <NavDropdown title={t("navbarHouse")}>
                <Link
                  smooth
                  className="dropdown-item"
                  activeClassName="active"
                  to="/about-us#"
                >
                  {t("navbarAboutUs")}
                </Link>
                <Link
                  smooth
                  className="dropdown-item"
                  activeClassName="active"
                  to="/contact-us#"
                >
                  {t("navbarContactUs")}
                </Link>
                <Link
                  smooth
                  className="dropdown-item"
                  activeClassName="active"
                  to="/house-rules#"
                >
                  {t("navbarHouseRules")}
                </Link>
                <Link
                  smooth
                  className="dropdown-item"
                  activeClassName="active"
                  to="/terms-and-conditions#"
                >
                  {t("navbarTermsAndConditions")}
                </Link>
              </NavDropdown>

              {Object.keys(apartments).length && (
                <NavDropdown title={t("navbarApartments")}>
                  {Object.entries(apartments).map((apartment, index) => {
                    return (
                      <Link
                        key={index}
                        smooth
                        className="dropdown-item"
                        activeClassName="active"
                        to={`/apartment/${apartment[0]}#`}
                      >
                        {apartment[1].title}
                      </Link>
                    );
                  })}
                </NavDropdown>
              )}

              <Link
                className="nav-link"
                smooth
                activeClassName="active"
                to="/contact-us#"
                scroll={(el) => scrollWithOffset(el)}
              >
                {t("navbarContactUs")}
              </Link>

              <NavDropdown title={lang[i18n.language]}>
                <Link
                  to=""
                  className="dropdown-item"
                  activeClassName="active"
                  onClick={(e) => {
                    e.preventDefault();
                    i18n.changeLanguage("en");
                  }}
                >
                  {t("navbarLanguagesEnglish")}
                </Link>
                <Link
                  to=""
                  className="dropdown-item"
                  activeClassName="active"
                  onClick={(e) => {
                    e.preventDefault();
                    i18n.changeLanguage("hr");
                  }}
                >
                  {t("navbarLanguagesCroatian")}
                </Link>
                <Link
                  to=""
                  className="dropdown-item"
                  activeClassName="active"
                  onClick={(e) => {
                    e.preventDefault();
                    i18n.changeLanguage("it");
                  }}
                >
                  {t("navbarLanguagesItalian")}
                </Link>
                <Link
                  to=""
                  className="dropdown-item"
                  activeClassName="active"
                  onClick={(e) => {
                    e.preventDefault();
                    i18n.changeLanguage("de");
                  }}
                >
                  {t("navbarLanguagesDeutsch")}
                </Link>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
);

export default MainNavigation;
